.magichr {
	height: 1px;
	width: 220px;
	margin: 0 auto 1.5em;
	background: linear-gradient(90deg, #05FBFF 0%, #330867 100%);
}

.circle {
	width: 700px;
	height: 700px;
	padding: 2px;
	border-radius: 100%;
	position: absolute;
	top: -100px;
	z-index: -1;
	background: linear-gradient(to bottom, #05FBFF, #330867);
	opacity: 0.2;
}

.circle div {
	content: "";
	display: block;
	width: 100%;
	height: 100%;
	border-radius: 100%;
	background: #FCFEFF;
}

.circle-right {
	right: calc(-700px / 2);
}

.circle-left {
	left: calc(-700px / 2);
}

.gh-section-dr {
	background-image: linear-gradient(222deg, #059E9E 0%, #330867 100%);
	overflow: hidden;
}

.bubble {
	position: absolute;
	background: rgba(255, 255, 255, 0.05);
	border-radius: 100%;
	mix-blend-mode: soft-light;
}

.bubble-1 {
	height: 1000px;
	width: 1000px;
	top: -60%;
	left: -20%;
}

.bubble-2 {
	height: 555px;
	width: 555px;
	top: 20%;
	right: calc(-555px * 0.6);
}

.bubble-3 {
	height: 1350px;
	width: 1350px;
	bottom: calc(-1350px * 0.8);
	left: calc(50% - 675px);
	background: rgba(255, 255, 255, 0.06);
}

